import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useAdmissionRemoveOuterGroup } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionSubjectCriteriaCardMenu = ({
  aid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [admissionRemoveOuterGroup] = useAdmissionRemoveOuterGroup();

  const onDelete = () => {
    if (menuData?._id && aid) {
      setDisabled((pre) => !pre);
      admissionRemoveOuterGroup({
        aid: aid,
        bodyParameter: {
          sgid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default AdmissionSubjectCriteriaCardMenu;
