import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import AdmissionSubjectCriteriaCardMenu from "./AdmissionSubjectCriteriaCardMenu";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const AdmissionSubjectCriteriaCard = ({ subCri, aid, onRefetch }) => {
  const getQuery = useLocation();
  const params = useParams();
  const [open, setOpen] = useState(false);
  const onToggle = (e) => {
    e.preventDefault();
    setOpen((pre) => !pre);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/admission?type=criteria-detail`}
        state={{
          ...getQuery.state,
          // headId: subCri,
          subCri: subCri,
        }}
      >
        <div className={style.smc_card}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner}>
              <div className={style.smc_card_contatiner_inner}>
                <h6>{subCri?.subject_group_name}</h6>
              </div>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          />
        </div>
      </Link>
      {open && (
        <AdmissionSubjectCriteriaCardMenu
          onClose={() => setOpen((pre) => !pre)}
          aid={aid}
          menuData={subCri}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AdmissionSubjectCriteriaCard;
