import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useAdmissionOuterGroupList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import AdmissionSubjectCriteriaCard from "./AdmissionSubjectCriteriaCard";
import AddAdmissionSubjectCriteria from "./AddAdmissionSubjectCriteria";
import style from "../../FinanceManager/FinanceManager.module.css";

const AdmissionSubjectCriteria = ({ aid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOuterGroupList,
    admissionOuterGroupListLoading,
    admissionOuterGroupListRefetch,
  } = useAdmissionOuterGroupList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOuterGroupList?.list?.length) {
      let obj = nextPage(admissionOuterGroupList?.list?.length);
      setState(() => obj);
    }
  }, [admissionOuterGroupList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    admissionOuterGroupListRefetch();
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("subject_criteria")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {admissionOuterGroupList?.list?.map((subCri, index) =>
            admissionOuterGroupList?.list?.length === index + 1 ? (
              <div key={subCri?._id} ref={ref}>
                <AdmissionSubjectCriteriaCard
                  subCri={subCri}
                  onRefetch={onRefetch}
                  aid={aid}
                />
              </div>
            ) : (
              <AdmissionSubjectCriteriaCard
                subCri={subCri}
                key={subCri?._id}
                onRefetch={onRefetch}
                aid={aid}
              />
            )
          )}

          {admissionOuterGroupListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOuterGroupList?.list?.length === 0 && (
              <EmptyMessage
                title="No item found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            admissionOuterGroupList?.list?.length === 0 && (
              <EmptyMessage
                title="No item."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddAdmissionSubjectCriteria
          onClose={onToggle}
          aid={aid}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AdmissionSubjectCriteria;
